import React, { useState } from 'react'
import { Modal, Button, Space } from 'antd';

const ReachableContext = React.createContext();
const UnreachableContext = React.createContext();

function TableCell({ obj, row, state, setState }) {

    let cellId = row.toString() + obj.column.toString()
    let stateCellId = state ? state.cellid : null
    let blank = <div>&nbsp;</div>

    const [modal, contextHolder] = Modal.useModal();

    const config = {
        title: 'Use Hook!',
        content: (
            <>
                <ReachableContext.Consumer></ReachableContext.Consumer>
                <br />
                <UnreachableContext.Consumer></UnreachableContext.Consumer>
            </>
        ),
    };


    let handletdClick = (row, column, isBooked) => {


        if (isBooked) {

            Modal.warning({
                content: 'This position is already assigned...',
              });
        console.log("ok");

        }
        else {

    setState({ cellid: cellId, row, column })

}

    }


return (
    <td style={{ fontSize: "13px" }} className={obj.isBooked ? "position-selector-td-booked" : stateCellId == cellId ? "position-selector-td-selected" : "position-selector-td"} onClick={() => { handletdClick(row, obj.column, obj.isBooked) }}>
        {blank}
    </td>
)
}

export default TableCell
