export let CREATE_EVENT_LAYOUT = async (row, column) => {

    let layout = []

    for (let i = 0; i < row; i++) {

        let temp_row = []

        for (let j = 0; j < column; j++) {

            temp_row.push({ row: i, column: j, isBooked: false })

        }

        layout.push(temp_row)

    }

    //console.log(layout);

    return layout


}



